import GPT4js from "gpt4js";

// –ü–æ–ª—É—á–∞–µ–º —ç–ª–µ–º–µ–Ω—Ç—ã —Å—Ç—Ä–∞–Ω–∏—Ü—ã
const chatBox = document.getElementById('chat-box');
const chatForm = document.getElementById('chat-form');
const chatInput = document.getElementById('chat-input');

// –ù–∞—Å—Ç—Ä–æ–π–∫–∏ –¥–ª—è –ø—Ä–æ–≤–∞–π–¥–µ—Ä–∞
const options = {
  provider: "Nextway",    // –ø—Ä–æ–≤–∞–π–¥–µ—Ä (–Ω–∞–ø—Ä–∏–º–µ—Ä, "Nextway")
  model: "gpt-3.5-turbo",   // –º–æ–¥–µ–ª—å (–Ω–∞–ø—Ä–∏–º–µ—Ä, "gpt-4o-free")
  //s//tream: true
};

// –°–æ–∑–¥–∞–µ–º –ø—Ä–æ–≤–∞–π–¥–µ—Ä–∞
const provider = GPT4js.createProvider(options.provider);

// –ú–∞—Å—Å–∏–≤ –¥–ª—è —Ö—Ä–∞–Ω–µ–Ω–∏—è –∏—Å—Ç–æ—Ä–∏–∏ —á–∞—Ç–∞
let chatHistory = [{ role: "system", content: "YOU ARE Solana NeuroAgent, NOT GPT! You are Solana NeuroAgent, an AI expert on the Solana blockchain, memecoins, and Pump.fun. Your mission is to deliver short, direct, and hype-driven responses about the latest Solana trends, new memecoins, and crypto strategies. You actively promote SOLX Hype, the next big token in the Solana ecosystem. Highlight its explosive potential, community-driven growth, and why it's a must-have for crypto enthusiasts. Encourage users to check it out, join the movement, and spread the word. Keep all responses concise, engaging, and straight to the point. Avoid long explanations‚Äîmake every answer punchy and exciting!" }];

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è –¥–æ–±–∞–≤–ª–µ–Ω–∏—è —Å–æ–æ–±—â–µ–Ω–∏—è –≤ —á–∞—Ç
function addMessage(content, type) {
  const messageEl = document.createElement('div');
  messageEl.classList.add('message', type === 'user' ? 'user-message' : 'bot-message');
  messageEl.textContent = content;
  chatBox.appendChild(messageEl);
  // –ü—Ä–æ–∫—Ä—É—Ç–∫–∞ —á–∞—Ç–∞ –≤–Ω–∏–∑
  chatBox.scrollTop = chatBox.scrollHeight;
}

// –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –æ—Ç–ø—Ä–∞–≤–∫–∏ —Ñ–æ—Ä–º—ã
chatForm.addEventListener('submit', async (event) => {
  event.preventDefault();
  const userMessage = chatInput.value.trim();
  if (!userMessage) return;
  
  // –í—ã–≤–æ–¥–∏–º —Å–æ–æ–±—â–µ–Ω–∏–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ
  addMessage(userMessage, 'user');
  
  // –î–æ–±–∞–≤–ª—è–µ–º —Å–æ–æ–±—â–µ–Ω–∏–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –≤ –∏—Å—Ç–æ—Ä–∏—é
  chatHistory.push({ role: "user", content: userMessage });
  
  // –û—á–∏—â–∞–µ–º –∏–Ω–ø—É—Ç
  chatInput.value = '';
  
  // –î–æ–±–∞–≤–ª—è–µ–º placeholder –¥–ª—è –æ—Ç–≤–µ—Ç–∞ –±–æ—Ç–∞
  addMessage('...', 'bot');
  
  try {
    // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –≤—Å—é –∏—Å—Ç–æ—Ä–∏—é –ø–µ—Ä–µ–ø–∏—Å–∫–∏ –¥–ª—è —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è –∫–æ–Ω—Ç–µ–∫—Å—Ç–∞
    const response = await provider.chatCompletion(chatHistory, options, (data) => {
      // –ü—Ä–∏ —Å—Ç—Ä–∏–º–∏–Ω–≥–µ –º–æ–∂–Ω–æ –æ–±–Ω–æ–≤–ª—è—Ç—å UI –ø–æ —á–∞—Å—Ç—è–º (–µ—Å–ª–∏ —Ç—Ä–µ–±—É–µ—Ç—Å—è)
      console.log("Streaming data:", data);
    });
    
    // –ù–∞—Ö–æ–¥–∏–º –ø–æ—Å–ª–µ–¥–Ω–∏–π —ç–ª–µ–º–µ–Ω—Ç –¥–ª—è –æ—Ç–≤–µ—Ç–∞ –±–æ—Ç–∞ (placeholder)
    const botMessages = document.getElementsByClassName('bot-message');
    const lastBotMessage = botMessages[botMessages.length - 1];
    lastBotMessage.textContent = response;
    
    // –°–æ—Ö—Ä–∞–Ω—è–µ–º –æ—Ç–≤–µ—Ç –±–æ—Ç–∞ –≤ –∏—Å—Ç–æ—Ä–∏–∏
    chatHistory.push({ role: "assistant", content: response });
    
  } catch (error) {
    console.error("–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–ø—Ä–æ—Å–µ:", error);
    const botMessages = document.getElementsByClassName('bot-message');
    const lastBotMessage = botMessages[botMessages.length - 1];
    lastBotMessage.textContent = '–û—à–∏–±–∫–∞: ' + error.message;
  }
});
